form {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 30px;

  input {
    width: 100%;
    padding: 4px 0;
    border: none;
    border-bottom: 1px solid $color-light-slate-grey;
    background-color: $color-transparent;
    color: $color-default-white;
    font: 400 14px/120% $ff-roboto-condensed;

    &::placeholder {
      color: $color-default-white;
    }

    @media (hover: hover), screen and (min-width: 0\0) {
      &:focus {
        outline: 3px solid $color-torch-red;
        outline-offset: 5px;
      }
    }
  }

  .btn {
    padding: 17px 24px;
    grid-column: 1 / -1;
    border-width: 2px;
    font-size: 14px;
  }
}
